#header {
  position: relative;
  width: 100%;
  background: #FFFFFF;
  padding: 0;
  z-index: 998;
  @include media('tb') {
    position: sticky;
    top: 0;
    left: 0;
    height: 60px;
    box-shadow: 0px 6px 60px rgba($color: #000000, $alpha: 0.15);
  }
  .container {
    position: relative;
  }
  .header_wrapper {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 16px 0 14px;
    margin: 0;
    @include media('tb') {
      padding: 15px 0 13px;
    }
    .logo {
      position: relative;
      flex: 0 0 auto;
      width: 165px;
      padding: 0;
      @include media('tb') {
        width: 99px;
      } 
    }
    .intro {
      position: relative;
      flex: 1 auto;
      padding: 0;
      width: calc(100% - 165px);
      max-width: 100%;
      padding-left: 50px;
      @include media('tb') {
        display: none;
      }
      h2 {
        position: relative;
        font-weight: normal;
        font-size: 16px;
        letter-spacing: 0.05em;
        line-height: 1.5;
        text-align: left;
        color: #333333;
        margin-bottom: 2px;
      }
      p {
        font-family: $font_roboto;
        font-weight: normal;
        font-size: 16px;
        letter-spacing: 0.05em;
        line-height: 1.5;
        text-align: left;
        color: #999999;
        padding-left: 8px;
      }
    }
  }
}

.logo {
  position: relative;
  padding: 0;
  margin: 0;
  a {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
    outline: none;
    width: 100%;
    margin: 0;
    img {
      width: 100%;
    }
  }
}

.nav_menu_container {
  position: sticky;
  top: 0;
  left: 0;
  width: 100%;
  background: #1A1A1A;
  padding: 0px 32px;
  box-shadow: 0px 6px 6px rgba(0, 0, 0, 0.4);
  z-index: 998;
  @include media('tb') {
    display: none;
  }
  .nav_menu {
    position: relative;
    list-style: none;
    display: flex;
    align-items: center;
    justify-content: center;
    line-height: 1;
    & > li {
      position: relative;
      padding: 0;
      &::after {
        content: "";
        position: absolute;
        top: 50%;
        right: 0;
        display: inline-block;
        width: 1px;
        height: 40px;
        background: #808080;
        transform: translateY(-50%);
      }
      &:first-child {
        &::before {
          content: "";
          position: absolute;
          top: 50%;
          left: 0;
          display: inline-block;
          width: 1px;
          height: 40px;
          background: #808080;
          transform: translateY(-50%);
        }
      }
      & > a {
        position: relative;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;
        font-size: 14px;
        font-weight: normal;
        line-height: 22px;
        color: #FFFFFF;
        padding: 10px 3.57rem;
        margin: 0;
        white-space: nowrap;
        text-decoration: none;
        height: 100%;
        cursor: pointer;
        width: fit-content;
        z-index: 0;
        span {
          position: relative;
          text-decoration: none;
          outline: none;
          color: inherit;
          z-index: 0;
        }
        .m {
          color: #FFFFFF;
        }
        .s {
          font-size: 12px;
          line-height: 18px;
          color: #999999;
        }
      }
    }
    .drop_down {
      ul {
        display: block;
        list-style: none;
        position: absolute;
        left: 0;
        top: calc(100% + 20px);
        z-index: 99;
        opacity: 0;
        visibility: hidden;
        padding: 0;
        transition: 0.3s ease-in-out;
        li {
          position: relative;
          min-width: 200px;
          padding: 0;
          &:not(:last-child) {
            border-bottom: 1px solid #808080;
          }
        }
        a {
          position: relative;
          display: block;
          font-size: 14px;
          font-weight: normal;
          line-height: 22px;
          color: #FFFFFF;
          padding: 12px 3.57rem;
          margin: 0;
          background: rgba($color: #1A1A1A, $alpha: 0.8);
          white-space: nowrap;
          text-decoration: none;
          cursor: pointer;
          transition: all 0.3 ease-in-out;
          z-index: 0;
          &:hover {
            background: #1A1A1A;
            color: #FFFFFF;
          }
        }
        .drop_down ul {
          top: 0;
          left: calc(100% - 30px);
        }
        
        .drop-down:hover > ul {
          opacity: 1;
          top: 0;
          left: 100%;
        }
      }
      &:hover {
        & > ul {
          opacity: 1;
          top: 100%;
          visibility: visible;
        }
      }
    }
  }
}
