#mobile_nav_toggle {
  position: fixed;
  right: 0;
  top: 0;
  width: 60px;
  height: 60px;
  padding: 12px 14px 28px;
  background: #111111;
  display: none;
  border: none;
  outline: none;
  cursor: pointer;
  z-index: 999;
  margin: 0;
  transition: all 0.4s;
  .drawer_icon {
    position: relative;
    display: block;
    width: 100%;
    height: 100%;
    margin: 0;
    span {
      display: inline-block;
      position: absolute;
      left: 0;
      width: 100%;
      height: 2px;
      background: #FFFFFF;
      transition: all 0.4s ease-in-out;
      transform-origin: center center;
      &:nth-child(1) {
        top: 0;
      }
      &:nth-child(2) {
        top: calc(50% - 1px);
      }
      &:nth-child(3) {
        bottom: 0;
      }
    }
  }
  .drawer_text {
    position: absolute;
    bottom: 11.5px;
    left: 15px;
    font-weight: normal;
    font-size: 10.5px;
    line-height: 1.0;
    color: #FFFFFF;
  }
  &.drawer_open {
    .drawer_icon {
      position: relative;
      span {
        &:nth-child(1) {
          transform: translateY(9px) rotate(-36deg);
        }
        &:nth-child(2) {
          opacity: 0;
          visibility: hidden;
        }
        &:nth-child(3) {
          transform: translateY(-9px) rotate(36deg);
        }
      }
    }
  }
}

@include media('tb') {
  #mobile_nav_toggle {
    display: inline-block;
  }
}

// Mobile Nav
#mobile_nav {
  position: fixed;
  top: 60px;
  bottom: 0;
  z-index: 997;
  right: -100%;
  width: 80%;
  max-width: 600px;
  overflow-y: auto;
  transition: all 0.4s;
  background: #FFFFFF;
  .nav_header {
    position: relative;
    padding: 10px;
    .logo {
      a {
        width: 72px;
      }
    }
  }
  .nav_body {
    position: relative;
    padding: 30px 16px 20px;
  }
  .mobile_nav_menu_container {
    position: relative;
    width: 100%;
  }
  .mobile_nav_menu {
    position: relative;
    list-style: none;
    & > li {
      position: relative;
      display: block;
      & > a {
        position: relative;
        display: block;
        width: 100%;
        font-weight: normal;
        font-size: 14px;
        line-height: 24px;
        text-align: left;
        color: #000000;
        padding: 16px 0;
        text-decoration: none;
        outline: none;
        border-bottom: 1px solid #000000;
      }
    }
    .drop_down {
      & > a {
        &::after {
          content: "";
          position: absolute;
          top: calc(50% - 3px);
          right: 0;
          display: inline-block;
          width: 10px;
          height: 6px;
          background-image: url(../img/common/icon-down.png);
          background-position: center;
          background-repeat: no-repeat;
          background-size: contain;
          z-index: 2;
        }
      }
      ul {
        display: none;
        overflow: hidden;
      }
      li {
        padding-left: 16px;
        a {
          position: relative;
          display: block;
          width: 100%;
          font-weight: normal;
          font-size: 14px;
          line-height: 24px;
          text-align: left;
          color: #000000;
          padding: 16px 0;
          text-decoration: none;
          outline: none;
          border-bottom: 1px solid #000000;
        }
      }
      &.active {
        & > a::after {
          transform: rotate(180deg);
          transform-origin: center center;
        }
      }
    }
  }
}

#mobile_body_overly {
  width: 100%;
  height: 100%;
  z-index: 996;
  top: 0;
  left: 0;
  position: fixed;
  background: rgba($color: #000000, $alpha: 0.5);
  display: none;
}


/* Mobile Nav body classes */
body.mobile_nav_active {
  @include media('sp') {
    overflow: hidden;
  }
}
body.mobile_nav_active #mobile_nav {
  right: 0;
}