#footer {
  position: relative;
  padding: 55px 0 55px;
  background: #FFFFFF;
  margin: 0;
  @include media('sp') {
    padding: 40px 0 30px;
  }
  .footer_inner_wrapper {
    position: relative;
    @include media('pc') {
      display: flex;
      flex-wrap: wrap;
      align-items: center;
      justify-content: center;
    }
    .footer_info {
      padding-left: 30px;
      @include media('sp') {
        padding: 24px 0 0;
        width: fit-content;
        max-width: 100%;
        margin-left: auto;
        margin-right: auto;
      }
    }
  }
  .footer_logo {
    position: relative;
    @include media('sp') {
      width: 100%;
    }
    a {
      position: relative;
      display: flex;
      align-items: center;
      justify-content: center;
      width: 250px;
      text-decoration: none;
      outline: none;
      @include media('sp') {
        width: 150px;
        margin-left: auto;
        margin-right: auto;
      }
      img {
        width: 100%;
      }
    }
  }
  .footer_info {
    position: relative;
    .address {
      position: relative;
      font-size: 12px;
      line-height: 20px;
      font-weight: 300;
      color: #505050;
      margin-bottom: 2px;
    }
    .phone {
      position: relative;
      line-height: 1;
      @include media('sp') {
        text-align: center;
      }
      .link {
        position: relative;
        display: inline-block;
        font-family: $font_roboto;
        font-weight: 300;
        font-size: 24px;
        line-height: 1.2;
        letter-spacing: 0.1em;
        color: #505050;
        text-decoration: none;
        outline: none;
        &:hover {
          color: #000000;
        }
      }
    }
    .mail {
      position: relative;
      line-height: 1;
      @include media('sp') {
        text-align: center;
      }
      .link {
        position: relative;
        display: inline-block;
        font-family: $font_noto;
        font-weight: 300;
        font-size: 14px;
        line-height: 1.4;
        color: #505050;
        text-decoration: none;
        outline: none;
        &:hover {
          color: #000000;
        }
      }
    }
  }
}

.copyright {
  position: relative;
  display: block;
  background: #4D4D4D;
  padding: 12px 32px;
  font-family: $font_noto;
  font-weight: normal;
  font-size: 12px;
  line-height: 20px;
  text-align: center;
  color: #FFFFFF;
  @include media('sp') {
    padding: 10px 16px;
  }
}

.back_to_top {
  position: fixed;
  bottom: 30px;
  right: 32px;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 50px;
  text-decoration: none;
  outline: none;
  cursor: pointer;
  z-index: 996;
  @include media('sp') {
    width: 45px;
    right: 16px;
    bottom: 16px;
  }
  img {
    width: 100%;
    @include media('pc') {
      transition: all 0.4s;
    }
  }
  @include media('pc') {
    &:hover {
      text-decoration: none;
      img {
        filter: drop-shadow(0 0 12px #fff);
        -webkit-filter: drop-shadow(0 0 12px #fff);
      }
    }
  }
}

.sp_phone_action {
  display: none;
  @include media('tb') {
    position: fixed;
    top: 13px;
    right: 78px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 29px;
    z-index: 999;
    text-decoration: none;
    outline: none;
    .phone_icon {
      position: relative;
      width: 100%;
    }
    img {
      width: 100%;
    }
  }
}

.sp_mail_action {
  display: none;
  @include media('tb') {
    position: fixed;
    top: 16px;
    right: 128px;
    display: flex;
    align-items: center;
    justify-content: center;
    width: 38px;
    z-index: 999;
    text-decoration: none;
    outline: none;
    .phone_icon {
      position: relative;
      width: 100%;
    }
    img {
      width: 100%;
    }
  }
}

.side_action {
  position: fixed;
  top: 240px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 35px;
  cursor: pointer;
  transition: all 0.4s ease-in-out;
  z-index: 995;
  @include media('sp') {
    top: 120px;
    width: 30px;
  }
  img {
    width: 100%;
  }
  &:hover {
    opacity: 0.7;
  }
}