// section

.top_common_section {
  position: relative;
  padding: 50px 0;
  @include media('sp') {
    padding: 40px 0;
  }
}
.p_main_section {
  position: relative;
  padding: 50px 0 60px;
  @include media('sp') {
    padding: 40px 0 50px;
  }
}

.p_common_section {
  position: relative;
  padding: 50px 0 60px;
  @include media('sp') {
    padding: 40px 0 50px;
  }
}

// section title {
.section_title {
  position: relative;
  padding: 12px 30px 13px;
  background: $blue_color;
  margin-bottom: 30px;
  @include media('sp') {
    padding: 12px 20px;
  }
  &::before {
    content: "";
    position: absolute;
    top: 2px;
    left: 2px;
    right: 2px;
    bottom: 2px;
    display: block;
    border: 1px solid #FFFFFF;
    z-index: 0;
  }
  .ttl {
    position: relative;
    font-size: 24px;
    line-height: 1.5;
    font-weight: 300;
    color: #FFFFFF;
    z-index: 3;
    @include media('sp') {
      font-size: 18px;
      line-height: 30px;
    }
  }
}

// text set

.txt {
  position: relative;
  font-size: 14px;
  line-height: 24px;
  font-weight: normal;
}
.m_txt {
  position: relative;
  font-weight: normal;
  font-size: 16px;
  line-height: 28px;
  @include media('sp') {
    font-size: 14px;
    line-height: 24px;
  }
}

.mx_txt {
  position: relative;
  font-weight: normal;
  font-size: 18px;
  line-height: 30px;
  @include media('sp') {
    font-size: 14px;
    line-height: 24px;
  }
}

.m_ttl {
  position: relative;
  font-weight: normal;
  font-size: 20px;
  line-height: 2px;
  @include media('sp') {
    font-size: 18px;
    line-height: 30px;
  }
}

.text_list {
  position: relative;
  list-style-type: "";
  padding-left: 18px;
}

.underline_bg {
  display: inline;
  background: linear-gradient(transparent 68%, #FBB03B 0%);
}

/* input */

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select,
textarea {
  position: relative;
  background-color: #FFFFFF;
  border: 1px solid #858585;
  box-sizing: border-box;
  padding: 6px 16px;
  width: 100%;
  font-weight: normal;
  font-size: 14px;
  line-height: 24px;
  text-align: left;
  color: #000000;
  outline: none !important;
  transition: all 0.3s;
  @include media('sp') {
    padding: 6px 12px;
  }
}

input[type="text"],
input[type="email"],
input[type="tel"],
input[type="number"],
input[type="password"],
select {
  height: 48px;
  @include media('sp') {
    height: 40px;
  }
}

input[type="text"]:focus,
input[type="email"]:focus,
input[type="tel"]:focus,
input[type="number"]:focus,
input[type="password"]:focus,
select:focus,
textarea:focus {
  border-color: #A10707;
}


/* btn */

.btn_wrap {
  position: relative;
}

.btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 8px 20px;
  max-width: 240px;
  width: 100%;
  height: 50px;
  background: #F2F2F2;
  border: 1px solid #858585;
  font-family: $font_noto;
  font-size: 14px;
  line-height: 24px;
  font-weight: 300;
  color: #1A1A1A;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.4s;
  cursor: pointer;
  @include media('sp') {
    height: 45px;
    max-width: 200px;
  }
  span {
    position: relative;
    color: inherit;
    text-decoration: none;
    z-index: 3;
  }
  &:hover {
    background: #A10707;
    border-color: #A10707;
    color: #FFFFFF;
  }
}

.viewmore_link_wrap {
  position: relative;
  .viewmore_link {
    position: relative;
    display: inline-block;
    font-family: $font_ubuntu;
    font-weight: bold;
    font-style: italic;
    font-size: 16px;
    line-height: 26px;
    color: $orange_color;
    text-decoration: none;
    &::before {
      content: "";
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background: $orange_color;
    }
    &::after {
      content: ">";
      display: inline-block;
      line-height: 1;
      margin-left: 6px;
      vertical-align: text-bottom;
    }
    
    &:hover {
      &::before {
        width: 100%;
        animation: link-lineloop 0.6s ease-out;
        -webkit-animation: link-lineloop 0.6s ease-out;
      }
    }
  }
}

.action_btn {
  position: relative;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 16px 16px;
  max-width: 400px;
  width: 100%;
  background: #1A1A1A;
  border: 1px solid #FFFFFF;
  border-radius: 10px;
  font-family: $font_noto;
  font-size: 16px;
  line-height: 26px;
  font-weight: 500;
  color: #FFFFFF;
  text-align: center;
  text-decoration: none;
  outline: none;
  transition: all 0.6s cubic-bezier(0.8, 0, 0.2, 1);
  cursor: pointer;
  z-index: 0;
  @include media('sp') {
    padding: 12px 16px;
    font-size: 14px;
    line-height: 24px;
    max-width: 275px;
  }
  span {
    position: relative;
    color: inherit;
    text-decoration: none;
    z-index: 3;
  }
  &::before {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    display: block;
    background: #FFFFFF;
    width: 100%;
    height: 100%;
    transition: transform 0.6s cubic-bezier(0.8, 0, 0.2, 1);
    transform-origin: right top;
    transform: scale(0, 1);
    z-index: 1;
  }
  &:hover {
    color: #000000;
    &::before {
      transform-origin: left top;
      transform: scale(1, 1);
    }
  }
}

// wp-pagination
.p_wp_pagination {
  position: relative;
  .wp-pagenavi {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    align-items: center;
    span,
    a {
      margin-left: 4px;
      margin-right: 4px;
      margin-bottom: 8px;
      padding: 8px 16px;
      min-width: 40px;
      border: 1px solid #999999;
      display: flex;
      justify-content: center;
      align-items: center;
      font-size: 12px;
      font-weight: 300;
      line-height: 20px;
      color: #FFFFFF;
      transition: all 0.4s;
      @include media('sp') {
        padding: 6px 10px;
        min-width: 36px;
        margin-left: 3px;
        margin-right: 3px;
        margin-bottom: 6px;
      }
      &:hover {
        background: #FFFFFF;
        border-color: #FFFFFF;
        color: #000000;
      }
    }
    span {
      border: 1px solid #FFFFFF;
      background: #FFFFFF;
      color: #000000;
      &.pages {
        display: none;
      }
    }
  }
}
