// wrapper
#wrapper {
  position: relative;
  width: 100%;
  background: #111111;
}

main {
  position: relative;
  &#main {
    min-height: calc( ( var(--vh, 1vh) * 100 ) - 387px );
  }
}

picture,
figure,
.image_wrap {
  position: relative;
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: center;
  overflow: hidden;
}

picture img,
figure img,
.image_wrap img {
  width: 100%;
  height: 100%;
  object-fit: cover;
  object-position: center;
}

.scroll {
  position: relative;
  max-width: 100%;
  width: 100%;
  overflow: auto;
}
/* svg */
svg {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: center;
}

// sp hide

@include media('sp') {
  .sp_hide {
    display: none !important;
  }
}


